import {Component, OnInit, Input} from '@angular/core';
import {ProductoRopa, ProductColor} from './interfaces';
import {CarritoService} from 'src/app/services/carrito.service';


@Component({
    selector: 'app-producto-ropa',
    templateUrl: './producto-ropa.component.html',
    styleUrls: ['./producto-ropa.component.scss']
})
export class ProductoRopaComponent implements OnInit {

    constructor(private carritoService: CarritoService) {}

    @Input() producto: ProductoRopa = {
        nombre: "producto",
        colores: [],
        fotoUrl: "",
        precio: 0,
        sizes: [],
        descripcion: '',
        id: ""
    }

    selectedColor: string = "";
    selectedSize: string = "";

    public get colorList(): ProductColor[] {
        let ret: ProductColor[] = [];

        ret = this.producto.colores.map(p => {
            let pc: ProductColor = {
                color: "",
                checked: false
            };
            pc.color = p;
            pc.checked = this.selectedColor === p;
            return pc;
        })

        return ret;
    }

    public get sizes(): string[] {
        let ret: string[] = [];
        ret = this.producto.sizes;
        return ret;
    }

    agregarACarrito(p: ProductoRopa) {
        this.carritoService.add(p);
    }

    setColor(pColor: string) {
        this.selectedColor = pColor;
    }

    setSize(p: string) {
        this.selectedSize = p;
    }

    ngOnInit(): void {
    }

}
