import {Injectable} from '@angular/core';
import {ProductoRopa} from '../components/producto-ropa/interfaces';

@Injectable({
    providedIn: 'root'
})
export class CarritoService {

    constructor() {}

    public carrito: ProductoRopa[] = [];

    public add(p: ProductoRopa) {
        this.carrito.push(p);
    }


    public get cantidad(): number {
        return this.carrito.length;
    }


    public getMensajeWsapp() {
        let msg = "Hola!, quisiera comprarte estos productos: %0a%0a";
        let total = 0;

        for (let i = 0; i < this.carrito.length; i++) {
            msg += ("%0a" + this.carrito[i].nombre);
            total += this.carrito[i].precio;
        }

        msg +="%0a%0aPor un total de: *$ " + total + "*";

        return "https://wa.me/5493415473805?text="+msg;
    }

}
